<template lang="html">
    <Panel :class="$style.panel" title="In">
        <section :class="$style.content">
            <div v-for="item in items" :class="$style.item" :key="item.catory">
                <img :src="item.catory" alt="">
                <section :class="$style.list">
                    <div v-for="product in item.list" :class="$style.product" :key="product.img">
                        <img :src="product.img" alt="">
                        <p :class="$style.title">{{ product.title }}</p>
                        <p :class="$style.price"><em>{{ product.price }} </em></p>
                        <article>
                            <div :class="$style.wrapper">
                                <div :class="$style.inner" :style="{width: product.progress+'%'}"/>
                            </div>
                            <p>{{ product.progress }}%</p>
                        </article>
                        <label v-if="product.label">{{ product.label }}</label>
                    </div>
                </section>
            </div>
        </section>
    </Panel>
</template>

<script type="text/ecmascript-6">
import Panel from "../core/panel.vue"
export default {
    components: {
        Panel,
    },
    data() {
        return {
            items: [{
                catory: "//img12.360buyimg.com/jrpmobile/jfs/t3406/210/2247820735/50952/86016eea/5846d8e7N20446dbd.png?width=750&height=200",
                list: [{
                    img: "//img30.360buyimg.com/cf/s250x250_jfs/t14881/283/656508232/143980/9b606acf/5a33a171N18c9c80a.jpg",
                    title: "",
                    price: "29.00",
                    progress: 172,
                    label: "",
                }, {
                    img: "//img30.360buyimg.com/cf/s250x250_jfs/t15529/308/632300625/135123/adb9c6d0/5a37676dNc31da9ea.jpg",
                    title: "",
                    price: "998.00",
                    progress: 259,
                }],
            }, {
                catory: "//img12.360buyimg.com/jrpmobile/jfs/t3739/82/2244582797/54399/746c7d79/5846a953N66b67d05.png?width=750&height=200",
                list: [{
                    img: "//img30.360buyimg.com/cf/s250x250_jfs/t11257/287/2281727659/55759/e00b90a/5a138f21N32dca834.jpg",
                    title: "",
                    price: "19.00",
                    progress: 481,
                }, {
                    img: "//img30.360buyimg.com/cf/s250x250_jfs/t14209/293/662014092/61635/bba5c330/5a33a983N077cac56.jpg",
                    title: "",
                    price: "258.00",
                    progress: 105,
                }],
            }, {
                catory: "//img12.360buyimg.com/jrpmobile/jfs/t3214/113/4408032436/61548/ce316eef/5846d310Ne93f00c9.png?width=750&height=200",
                list: [{
                    img: "//img30.360buyimg.com/cf/s250x250_jfs/t15007/47/884045069/149879/17987250/5a3cb182N15868ea1.jpg",
                    title: "",
                    price: "99.00",
                    progress: 199,
                }, {
                    img: "//img30.360buyimg.com/cf/s250x250_jfs/t14197/160/546457496/134173/4e1baccd/5a30ccc5Na44f7492.jpg",
                    title: "",
                    price: "58.00",
                    progress: 222,
                }],
            }],
        }
    },
}
</script>

<style lang="scss" module>
    @import "../../css/element.scss";
    .panel {
        @include panel;
        .content {
            @include list;
            .item {
                >img {
                    display: block;
                    width: 100%;
                    height: 170px;
                }
                .list {
                    @include list(row);
                    .product {
                        width: 50%;
                        height: 396px;
                        box-sizing: border-box;
                        background: rgba(0,0,0,.02);
                        text-align: center;
                        position: relative;
                        img {
                            width: 180px;
                            height: 180px;
                            margin: 40px auto 20px;
                        }
                        .title {
                            font-family: PingFangSC-Medium;
                            font-size: 28px;
                            color: #444;
                        }
                        .price {
                            height: 48px;
                            line-height: 48px;
                            font-family: PingFangSC-Semibold;
                            font-size: 20px;
                            color: #999;
                            em {
                                margin-right: 4px;
                                font-size: 36px;
                                color: #FF3232;
                                &:before {
                                    content: "";
                                    font-size: 28px;
                                }
                            }
                        }
                        article {
                            @include list(row);
                            align-items: center;
                            justify-content: space-around;
                            padding: 0 20px;
                            height: 30px;
                            line-height: 30px;
                            margin-top: 16px;
                            .wrapper {
                                width: 144px;
                                height: 10px;
                                background: #F6E6E6;
                                border-radius: 22px;
                                .inner {
                                    width: 40%;
                                    max-width: 100%;
                                    height: 10px;
                                    background-image: linear-gradient(270deg, #F55 1%, #FF9C31 100%);
                                    border-radius: 22px;
                                }
                            }
                            p {
                                font-size: 24px;
                                color: #999;
                            }
                        }
                        label {
                            position: absolute;
                            top: 20px;
                            left: 20px;
                            font-family: PingFangSC-Medium;
                            font-size: 20px;
                            color: #ff0000;
                            text-align: center;
                            padding: 4px 8px;
                            background-image: linear-gradient(-35deg,#C1C1C1 0,#ACACAC 100%);
                        }
                    }
                }
            }
        }
    }
</style>



