<template lang="html">
    <Panel :class="$style.panel" title="">
        <section :class="$style.content">
            <div :class="$style.item">
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t4378/62/155957680/9017/16463551/58affd58Nb5c9198e.jpg?width=120&height=120" alt="">
                <div>
                    <h4></h4>
                    <p>188</p>
                </div>
                <em> > </em>
            </div>
            <div :class="$style.item">
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t4150/330/141981421/9147/604e3e04/58affc3bNbfa8f9d7.jpg?width=120&height=120" alt="">
                <div>
                    <h4></h4>
                    <p>158</p>
                </div>
                <em> > </em>
            </div>
            <div :class="$style.item">
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t4270/96/1102370053/8651/58b7fa5a/58bd2e10N453e9464.jpg?width=120&height=120" alt="">
                <div>
                    <h4></h4>
                    <p>app</p>
                </div>
                <em> > </em>
            </div>
        </section>
    </Panel>
</template>

<script type="text/ecmascript-6">
import Panel from "../core/panel.vue"
export default {
    components: {
        Panel,
    },
    data() {
        return {

        }
    },
}
</script>

<style lang="scss" module>
    @import "../../css/element.scss";
    .panel {
        @include panel;
        >h4 {
            border-bottom: 1px solid #ddd;
        }
        .content {
            @include list;
            .item {
                @include list(row);
                position: relative;
                height: 140px;
                align-items: center;
                &:after {
                    display: block;
                    content: " ";
                    height: 0;
                    width: 100%;
                    margin-left: 150px;
                    border-bottom: 1px solid #ddd;
                }
                &:last-child:after {
                    border-color: #fff;
                }
                >img {
                    display: inline-block;
                    width: 88px;
                    height: 88px;
                    margin: 0 32px;
                }
                >div {
                    width: 326px;
                    line-height: 1.8;
                    font-size: 32px;
                    color: #333;
                    p {
                        line-height: 1.1;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-size: 24px;
                        color: #999;
                    }
                }
                >em {
                    position: absolute;
                    right: 20px;
                    font-size: 28px;
                    color: #FF801A;
                }
            }
        }
    }
</style>



