<template lang="html">
    <Panel :class="$style.panel" title="">
        <section :class="$style.content">
            <div class="arrow"></div>
            <Slider :swiper-option="swiperOptions" :items="items" cname="product-slider"/>
        </section>
    </Panel>
</template>

<script type="text/ecmascript-6">
import Panel from "../core/panel.vue"
import Slider from "../core/slider.vue"
export default {
    components: {
        Panel,
        Slider,
    },
    data() {
        return {
            swiperOptions: {
                slidesPerView: 2.3,
                spaceBetween: 30,
                freeMode: true,
                // pagination: {
                //     el: ".swiper-pagination",
                //     clickable: true,
                // },
                // navigation: {
                //     nextEl: ".swiper-button-next",
                //     prevEl: ".swiper-button-prev",
                // },
            },
            items: [{
                href: "home",
                src: "//img12.360buyimg.com/jrpmobile/jfs/t13963/267/2355123229/48850/254f797a/5a3c59aeN9cb550f6.jpg?width=335&height=421",
            }, {
                href: "home",
                src: "//img12.360buyimg.com/jrpmobile/jfs/t13021/306/1996997425/21953/bda69db3/5a2f6a17N9da099b1.jpg?width=335&height=421",
            }, {
                href: "home",
                src: "//img12.360buyimg.com/jrpmobile/jfs/t14125/310/1997192193/46667/3c910f8b/5a2f6a36Nad95b650.jpg?width=335&height=421",
            }, {
                href: "home",
                src: "//img12.360buyimg.com/jrpmobile/jfs/t15784/188/381232069/39444/8878571d/5a2f6a4aNbd5a574c.jpg?width=335&height=421",
            }],
        }
    },
}
</script>

<style lang="scss">
    .product-slider {
        .swiper-container {
            padding: 0 24px;
            box-sizing: border-box;
            .swiper-slide {
                a {
                    display: inline-block;
                    width: 100%;
                    img {
                        display: block;
                        width: 100%;
                        height: 314px;
                        border: 1px solid #fafafa;
                    }
                }
            }
        }
    }
</style>

<style lang="scss" module>
    @import "../../css/element.scss";
    .panel {
        @include panel;
        .content {
            position: relative;
            padding-bottom: 40px;
            &>div {
                position: absolute;
                top: -68px;
                right: 30px;
                font-size: 28px;
                color: #999;
            }
        }
    }
</style>



