<template lang="html">
    <Panel :class="$style.panel" title="">
        <section :class="$style.content">
            <dl v-for="item in items" :class="$style.item" :key="item.title">
                <dt>{{ item.title }}<span>{{ item.sub }}</span></dt>
                <dd>{{ item.rate }}</dd>
                <dd>{{ item.text }}</dd>
            </dl>
        </section>
    </Panel>
</template>

<script type='text/ecmascript-6'>
import Panel from "../core/panel.vue"
export default {
    components: {
        Panel,
    },
    data() {
        return {
            items: [{
                title: "",
                sub: "",
                rate: "5.60%",
                text: "",
            }, {
                title: "",
                sub: "",
                rate: "4.22%",
                text: "7",
            }, {
                title: "",
                sub: "",
                rate: "5%",
                text: "",
            }, {
                title: "",
                sub: "",
                rate: "4.27%",
                text: "7",
            }],
        }
    },
}
</script>

<style lang='scss' module>
    @import "../../css/element.scss";
    .panel {
        @include panel;
        .content {
            @include list(row);
            justify-content: space-around;
            box-sizing: border-box;
            &:after{
                display: block;
                content: " ";
                width: 100%;
                height: 0;
                box-sizing: border-box;
                border-bottom: 1px solid #ddd;
                position: relative;
                top: -208px;
            }
            .item {
                position: relative;
                box-sizing: border-box;
                width: 50%;
                padding: 34px 16px;
                &:after {
                    display: block;
                    content: " ";
                    position: absolute;
                    top: 50%;
                    right: 0;
                    width: 1px;
                    height: 136px;
                    margin-top: -68px;
                    border-right: 1px solid #ddd;
                }
                &:nth-child(2n){
                    &:after {
                        display: none;
                    }
                }
                dt {
                    font-size: 30px;
                    line-height: 42px;
                    color: #333;
                    span {
                        padding: 0 8px;
                        vertical-align: 4px;
                        margin-left: 2px;
                        border: 1px solid #ff5155;
                        font-size: 22px;
                        color: #ff5155;
                    }
                }
                dd {
                    &:nth-child(2){
                        height: 58px;
                        line-height: 58px;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        font-weight: 700;
                        font-size: 44px;
                        color: #ff5155;
                    }
                    &:nth-child(3){
                       height: 34px;
                       line-height: 34px;
                       white-space: nowrap;
                       text-overflow: ellipsis;
                       overflow: hidden;
                       font-size: 24px;
                       color: #999;
                    }
                }
            }
        }
    }
</style>



